.error-ns-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 50px 0;
  background-color: #e7f3f7;
  text-align: center;
  z-index: 1000;
  width:100%;
  height:100%;
}

.error-ns-btn-group {
  position: relative;
  margin: 100px auto;
  width: 170px;
}

#error-ns-install, #error-ns-update {
  filter: alpha(opacity=0);
  cursor: pointer;
  position: absolute;
  z-index: 1001;
  width: 75px;
  height: 30px;
  left: 0;
}

#error-ns-update {
  left: 80px;
}
